import styled from "styled-components";

export const Toggle = styled.div`
  width: fit-content;
  min-width: 27px;
  max-width: 100px;
  margin-inline-end: 2rem;
  height: 30px;
  border: gray 2px solid;
  border-radius: 10px;
  transition: all 1s 0.5s ease-in;
  background: linear-gradient(
    66deg,
    ${(props) =>
      props.theme === "dark"
        ? ` rgba(13,163,154,1) 0%,
 rgba(17,157,187,1) 39%,
  rgba(19,142,175,1) 55%,
   rgba(15,53,80,0.6194852941176471) 100%`
        : ` rgba(20, 20, 20, 1) 0%,
    rgba(28, 31, 82, 1) 39%,
     rgba(41, 44, 130, 1) 55%,
     rgba(15, 23, 80, 1) 100%`}
  );
`;

export const ToggleButton = styled.button`
  background: ${(props) =>
    `
      url('${props.image || null}')`};
  width: 50px;
  background-repeat: no-repeat;
  height: 100%;
  border: none;
  background-size: contain;
  transition: all 0.5s ease-in;
`;
