import React from "react";
import { Wrapper, Inner, Text } from "./styles";
const HeaderBrowse = ({ image, title, text }) => {
  return (
    <Wrapper image={image}>
      <Inner>
        <Text>
          <h1>{title}</h1>
          <p>{text}</p>
        </Text>
      </Inner>
    </Wrapper>
  );
};

export default HeaderBrowse;
