import React from "react";
import { Wrapper, Container, Inner } from "./styles";
import GridTemplate from "./GridTemplate";
import Button from "../Button";
import Thumb from "../Thumb";
import Spinner from "../Spinner";
const GridContainer = ({
  type,
  state,
  loading,
  setIsLoading,
  image,
  clickable,
}) => {
  return (
    <Wrapper>
      <Container>
        <GridTemplate>
          {state.results.map((img) => {
            return (
              <Inner key={img.id}>
                <Thumb
                  src={`${image}${img.poster_path}`}
                  clickable
                  movieId={img.id}
                  type={type}
                />
              </Inner>
            );
          })}
        </GridTemplate>
        {loading && <Spinner />}
        {!loading && (
          <Button text={"Load More"} callback={() => setIsLoading(true)} />
        )}
      </Container>
    </Wrapper>
  );
};

export default GridContainer;
