import React from "react";
import { Nav, HeaderBrowse } from "../components";
import { useMovieFetch } from "../hooks/useFetchMovies";
import { IMAGE_URL, BACKDROP_SIZE, POSTER_SIZE } from "../api/config";
import GridContainer from "../components/Movies/Grid/Grid";
import PageName from "../components/Movies/Pagename/PageName";
import SearchBar from "../components/Movies/SearchBar";
const Browse = () => {
  const {
    state,
    searchTerm,
    type,
    setIsLoading,
    setSearchTerm,
    loading,
    setType,
  } = useMovieFetch();
  return (
    <>
      {!searchTerm && state.results[1] ? (
        <>
          <Nav />
          <HeaderBrowse
            image={`${IMAGE_URL}${BACKDROP_SIZE}${state.results[1].backdrop_path}`}
            title={
              type === "movie"
                ? state.results[1].original_title
                : state.results[1].original_name
            }
            text={state.results[1].overview}
          />
        </>
      ) : null}
      <SearchBar setSearchTerm={setSearchTerm} settype={setType} type={type} />
      <PageName setType={setType} />
      <GridContainer
        image={`${IMAGE_URL}${POSTER_SIZE}`}
        state={state}
        setIsLoading={setIsLoading}
        loading={loading}
        clickable={true}
        type={type}
      />
    </>
  );
};
export default Browse;
