import React from "react";
import { POSTER_SIZE, IMAGE_URL } from "../../../api/config";
import { Wrapper, Container, Inner, Image, Name, Title } from "./styles";

const Seasons = ({ seasons, callback }) => {
  return (
    <Wrapper>
      <Title>SEASONS</Title>
      <Container>
        {seasons.seasons.map((season) => {
          if (season.poster_path) {
            return (
              <Inner key={season.id}>
                <Image
                  src={`${IMAGE_URL}${POSTER_SIZE}${season.poster_path}`}
                />
                <Name>{season.name}</Name>
              </Inner>
            );
          } else {
            return null;
          }
        })}
      </Container>
    </Wrapper>
  );
};

export default Seasons;
