import React from "react";
import { IMAGE_URL, BACKDROP_SIZE, POSTER_SIZE } from "../api/config";
import { MovieHeader, Nav, BreadCrumb } from "../components";
const MovieContainer = ({ movie }) => {
  const Words = movie.overview;
  let word = Words.substring(0, 433);

  return (
    <>
      <Nav />
      <BreadCrumb movieTitle={movie.title || movie.name} />
      <MovieHeader image={`${IMAGE_URL}${BACKDROP_SIZE}${movie.backdrop_path}`}>
        <MovieHeader.Container>
          <MovieHeader.PaneLeft>
            <MovieHeader.Image
              src={`${IMAGE_URL}${POSTER_SIZE}${movie.poster_path}`}
              alt={movie.title}
            />
          </MovieHeader.PaneLeft>
          <MovieHeader.PaneRight>
            <MovieHeader.SubTitle>{movie.name}</MovieHeader.SubTitle>
            <MovieHeader.Text>
              {movie.overview.length > 500 ? word : movie.overview}
            </MovieHeader.Text>
            <MovieHeader.Frame>
              {movie.directors.length > 0 ? (
                <MovieHeader.Div>
                  <h3>
                    {movie.directors.length === 1 ? "DIRECTOR" : "DIRECTORS"}
                  </h3>
                </MovieHeader.Div>
              ) : (
                <MovieHeader.Div>
                  <h3>STATUS</h3>
                </MovieHeader.Div>
              )}
              {movie.release_date ? (
                <MovieHeader.Div>
                  <h3>RELEASE DATE</h3>
                </MovieHeader.Div>
              ) : (
                <MovieHeader.Div>
                  <h3>FIRST AIR DATE</h3>
                </MovieHeader.Div>
              )}
              {movie.runtime ? (
                <MovieHeader.Div>
                  <h3>RUNTIME</h3>
                </MovieHeader.Div>
              ) : (
                <MovieHeader.Div>
                  <h3>RATING</h3>
                </MovieHeader.Div>
              )}
            </MovieHeader.Frame>
            <MovieHeader.Frame>
              <MovieHeader.Div>
                {movie.directors.length > 0 ? (
                  movie.directors.map((director) => (
                    <MovieHeader.Text>{director.name}</MovieHeader.Text>
                  ))
                ) : (
                  <MovieHeader.Text>{movie.status}</MovieHeader.Text>
                )}
              </MovieHeader.Div>
              <MovieHeader.Div>
                <MovieHeader.Text>
                  {movie.release_date
                    ? movie.release_date
                    : movie.first_air_date}
                </MovieHeader.Text>
              </MovieHeader.Div>
              <MovieHeader.Div>
                <MovieHeader.Text>
                  {movie.runtime ? `${movie.runtime} mins` : movie.vote_average}
                </MovieHeader.Text>
              </MovieHeader.Div>
            </MovieHeader.Frame>
          </MovieHeader.PaneRight>
        </MovieHeader.Container>
      </MovieHeader>
    </>
  );
};

export default MovieContainer;
