import React, { createContext, useState, useContext } from "react";
import {
  Container,
  Body,
  Frame,
  Header,
  Inner,
  Item,
  Title,
} from "./styles/accordion";

const ToggleContext = createContext();

const Accodion = ({ children, ...restProps }) => {
  return (
    <Container {...restProps}>
      <Inner>{children}</Inner>
    </Container>
  );
};

Accodion.Title = function AccodionTitle({ children, ...restProps }) {
  return <Title {...restProps}>{children}</Title>;
};
Accodion.Inner = function AccodionInner({ children, ...restProps }) {
  return <Inner {...restProps}>{children}</Inner>;
};
Accodion.Body = function AccodionBody({ children, ...restProps }) {
  const { toggleShow } = useContext(ToggleContext);
  return toggleShow ? <Body {...restProps}>{children}</Body> : null;
};
Accodion.Frame = function AccodionFrame({ children, ...restProps }) {
  return <Frame {...restProps}>{children}</Frame>;
};
Accodion.Header = function AccodionHeader({ children, ...restProps }) {
  const { toggleShow, setToggleShow } = useContext(ToggleContext);
  return (
    <Header
      onClick={() => setToggleShow((toggleShow) => !toggleShow)}
      {...restProps}>
      {children}
      {toggleShow ? (
        <img src='/images/icons/close-slim.png' alt='close' />
      ) : (
        <img src='/images/icons/add.png' alt='open' />
      )}
    </Header>
  );
};
Accodion.Item = function AccodionItem({ children, ...restProps }) {
  const [toggleShow, setToggleShow] = useState(false);
  return (
    <ToggleContext.Provider value={{ toggleShow, setToggleShow }}>
      <Item {...restProps}>{children}</Item>
    </ToggleContext.Provider>
  );
};
export default Accodion;
