import React, { useState, useContext } from "react";
import { Form } from "../components";
import * as ROUTES from "../Routes/routes";
import { useNavigate } from "react-router-dom";
import { FirebaseContext } from "../context/firebase";
import {
  getAuth,
  createUserWithEmailAndPassword,
  updateProfile,
} from "firebase/auth";
import HeaderContainer from "../containers/Header";
import Footer from "../containers/Footer";

const Signup = () => {
  const history = useNavigate();
  const { app } = useContext(FirebaseContext);

  const [state, setState] = useState({
    firstName: "",
    lastName: "",
    emailAddress: "",
    password: "",
    error: "",
  });

  const { lastName, firstName, emailAddress, password, error } = state;
  const isInvalid =
    lastName === "" ||
    firstName === "" ||
    password === "" ||
    emailAddress === "";
  const random = Math.floor(Math.random() * 5) + 1;
  const handleSignup = (event) => {
    event.preventDefault();

    const auth = getAuth(app);
    createUserWithEmailAndPassword(auth, emailAddress, password)
      .then((userCredential) => {
        // Signed in
        const user = userCredential.user;
        updateProfile(auth.currentUser, {
          displayName: lastName + " " + firstName,
          photoURL: `/public/images/users/${random}.png`,
        });
        console.log(user);
        // ...
      })
      .then(() => {
        history(ROUTES.BROWSE);
      })
      .catch((error) => {
        setState({
          firstName: "",
          lastName: "",
          emailAddress: "",
          password: "",
          error: [error.code, error.message],
        });
        // ..
      });
  };
  return (
    <>
      <HeaderContainer>
        <Form>
          <Form.Title>Sign Up</Form.Title>
          {error && <Form.Error>{error}</Form.Error>}
          <Form.Base onSubmit={handleSignup} method='POST'>
            <Form.Input
              type='text'
              placeholder='Last Name'
              value={lastName}
              onChange={({ target }) =>
                setState({ ...state, lastName: target.value })
              }
            />
            <Form.Input
              type='text'
              placeholder='First Name'
              value={firstName}
              onChange={({ target }) =>
                setState({ ...state, firstName: target.value })
              }
            />
            <Form.Input
              type='email'
              placeholder='Email address'
              value={emailAddress}
              onChange={({ target }) =>
                setState({ ...state, emailAddress: target.value })
              }
            />
            <Form.Input
              type='password'
              autoComplete='off'
              placeholder='Password'
              value={password}
              onChange={({ target }) =>
                setState({ ...state, password: target.value })
              }
            />
            <Form.Submit disabled={isInvalid} type='submit'>
              Sign Up
            </Form.Submit>
          </Form.Base>
          <Form.Text>
            Already a user? <Form.Link to='/signin'> Sign In now</Form.Link>
          </Form.Text>
          <Form.TextSmall>
            This page is protected by Google reCAPTCHA to ensure you are not a
            bot. Learn more.
          </Form.TextSmall>
        </Form>
      </HeaderContainer>
      <Footer />
    </>
  );
};

export default Signup;
