import React from "react";
import MovieContainer from "../containers/movieHeader";
//import Hooks
import { useParams } from "react-router-dom";
import { useSeriesFetch } from "../hooks/useFetchSeries";
//import Components
import { Spinner, Seasons, Similar, YouTubes } from "../components";
const Series = () => {
  const { seriesId } = useParams();
  const { series, loading, error, setSeasonNumber } = useSeriesFetch(seriesId);
  console.log(series);
  if (loading) return <Spinner />;
  if (error) return <div>Something went wrong.....</div>;

  return (
    <>
      <MovieContainer movie={series} callback={setSeasonNumber} />
      {series.Video.results.length > 0 && (
        <YouTubes video={series.Video.results} />
      )}

      <Seasons seasons={series} />
      <Similar Similar={series.similar} />
    </>
  );
};

export default Series;
