import { useEffect, useState } from "react";
import API from "../api/API";

export const useSeriesFetch = (movieId) => {
  const [series, setSeries] = useState({});
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);
  const [seasonNumber, setSeasonNumber] = useState();

  useEffect(() => {
    const fetchSeries = async () => {
      try {
        setLoading(true);
        setError(false);

        const series = await API.fetchSeries(movieId);
        const credits = await API.fetchCredits(movieId, "tv");
        const similar = await API.fetchSimilar(movieId);
        const videos = await API.fetchVideo(movieId);
        // console.log(credits);
        // get director

        const directors = credits.crew.filter(
          (member) => member.job === "Director"
        );
        // console.log(directors);
        // console.log(movie);
        // console.log(credits.cast);
        setSeries({
          ...series,
          actors: credits.cast,
          crew: credits.crew,
          directors,
          similar: similar,
          Video: videos,
        });

        setLoading(false);
      } catch (error) {
        setError(true);
      }
    };

    fetchSeries();
  }, [movieId]);
  return { loading, series, error, seasonNumber, setSeasonNumber };
};
