import React, { useContext } from "react";
import { Inner, Wrapper, Logo } from "./styles";
import SunIcon from "../../../icons/sun.png";
import MoonIcon from "../../../icons/moon.png";
import ThemeToggler from "../../Toggle/Toggle";
import { Link as ReachRouterLink } from "react-router-dom";
import * as ROUTES from "../../../Routes/routes";
import logo from "../../../logo.svg";
import LogOut from "./Logout";
import { CurrentTheme } from "../../../context/currentType";
const Nav = () => {
  const { theme, toggleTheme } = useContext(CurrentTheme);

  return (
    <Wrapper theme={theme}>
      <Inner>
        <ReachRouterLink to={ROUTES.HOME}>
          <Logo src={logo} />
        </ReachRouterLink>
        <Inner>
          {" "}
          <ThemeToggler
            onClick={() => toggleTheme()}
            image={theme === "dark" ? SunIcon : MoonIcon}
            theme={theme}
          />
          <LogOut />
        </Inner>
      </Inner>
    </Wrapper>
  );
};

export default Nav;
