import faqData from "../fixtures/faqs.json";
import React from "react";
import { Accodion, OptForm } from "../components";

export function FaqsContainer() {
  return (
    <>
      <Accodion>
        <Accodion.Title>Frequently Asked Questions</Accodion.Title>
        {faqData.map((item) => (
          <Accodion.Item key={item.key}>
            <Accodion.Header>{item.header}</Accodion.Header>
            <Accodion.Body>{item.body}</Accodion.Body>
          </Accodion.Item>
        ))}
        <OptForm>
          <OptForm.Input placeholder='Email Address'></OptForm.Input>
          <OptForm.Button>Try it now</OptForm.Button>
          <OptForm.Text>
            Ready to watch? Enter your email to create or restart your
            membership
          </OptForm.Text>
        </OptForm>
      </Accodion>
    </>
  );
}
