import React, { useState } from "react";
import { ThemeProvider } from "styled-components";
import { darkTheme, lightTheme } from "./Theme/Theme";
import { useMovieFetch } from "./hooks/useFetchMovies";
import { GlobalStyles } from "./global-styles";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import * as ROUTES from "./Routes/routes";
import { RequireAuth, AlreadyAuth } from "./helpers/routes";
import { useAuthListener } from "./hooks";
import { Home, Browse, Signin, Signup, Movie, Series } from "./pages";
import { CurrentType, CurrentTheme } from "./context/currentType";
const App = () => {
  const [theme, setTheme] = useState("light");
  const toggleTheme = () => {
    if (theme === "light") {
      setTimeout(() => {
        setTheme("dark");
      }, 500);
    } else {
      setTimeout(() => {
        setTheme("light");
      }, 500);
    }
  };
  const { type } = useMovieFetch();
  const { user } = useAuthListener();
  return (
    <CurrentType.Provider value={type}>
      <CurrentTheme.Provider value={{ theme, toggleTheme }}>
        <ThemeProvider theme={theme === "light" ? lightTheme : darkTheme}>
          <Router>
            <Routes>
              <Route
                path={ROUTES.HOME}
                element={
                  <AlreadyAuth user={user}>
                    <Home />
                  </AlreadyAuth>
                }
              />
              <Route
                path={ROUTES.BROWSE}
                element={
                  <RequireAuth user={user}>
                    <Browse />
                  </RequireAuth>
                }
              />
              <Route
                path={ROUTES.SIGN_IN}
                element={
                  <AlreadyAuth user={user}>
                    <Signin />
                  </AlreadyAuth>
                }
              />
              <Route
                path={ROUTES.SIGN_UP}
                element={
                  <AlreadyAuth user={user}>
                    <Signup />
                  </AlreadyAuth>
                }
              />
              <Route path={ROUTES.Movie} element={<Movie />} />
              <Route path={ROUTES.TV} element={<Series />} />
            </Routes>
          </Router>
          <GlobalStyles />
        </ThemeProvider>
      </CurrentTheme.Provider>
    </CurrentType.Provider>
  );
};

export default App;
