import { useState, useEffect } from "react";
import API from "../api/API";
const initialState = {
  page: 1,
  results: [],
  total_pages: 0,
  total_results: 0,
};
export const useMovieFetch = () => {
  const [searchTerm, setSearchTerm] = useState("");
  const [state, setState] = useState(initialState);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [type, setType] = useState("tv");

  const fetchMovies = async (page, searchTerm = "", type) => {
    try {
      setError(false);
      setLoading(true);

      const movies = await API.fetchMovies(searchTerm, page, type);

      setState((prev) => ({
        ...movies,
        results:
          page > 1 ? [...prev.results, ...movies.results] : [...movies.results],
      }));
    } catch (error) {
      setError(true);
    }

    setLoading(false);
  };

  useEffect(() => {
    setState(initialState);
    fetchMovies(1, searchTerm, type);
  }, [searchTerm, type]);
  // load more
  useEffect(() => {
    if (!isLoading) return;
    fetchMovies(state.page + 1, searchTerm, type);
    setIsLoading(false);
  }, [isLoading, state.page, searchTerm, type]);
  return {
    setType,
    type,
    state,
    loading,
    error,
    setSearchTerm,
    searchTerm,
    setIsLoading,
  };
};
