const rem = (value) => {
  let rm = value / 16;
  return `${rm}rem`;
};

export const Wrapper = styled.nav`
  width: 100vw;
  z-index: 2;

  position: absolute;
`;
export const Inner = styled.div`
    padding: 0.5rem;
    display: flex;
    justify-content: space-between;
}`;
export const Logo = styled.img`
  height: 36px;
  width: ${rem(100)};
  margin-right: 40px;
  object-fit: contain;

  @media (min-width: 1449px) {
    height: 45px;
    width: 167px;
  }
`;
export const ButtonLink = styled.button`
  display: block;
  background-color: #e50914;
  width: 84px;
  height: fit-content;
  color: white;
  border: 0;
  align-self: center;
  font-size: ${rem(12)};
  border-radius: 3px;
  padding: 8px 17px;
  cursor: pointer;
  text-decoration: none;

  &:hover {
    background: #f40612;
  }
`;
