/* eslint-disable no-lone-blocks */
import React from "react";
import {
  Wrapper,
  Container,
  Image,
  PaneLeft,
  PaneRight,
  SubTitle,
  Text,
  Frame,
  Div,
} from "./styles";
const MovieHeader = ({ children, ...restProps }) => (
  <Wrapper {...restProps}>{children}</Wrapper>
);

MovieHeader.Container = function MovieHeaderContainer({
  children,
  ...restProps
}) {
  return <Container {...restProps}>{children}</Container>;
};
MovieHeader.PaneLeft = function MovieHeaderPaneLeft({
  children,
  ...restProps
}) {
  return <PaneLeft {...restProps}>{children}</PaneLeft>;
};
MovieHeader.PaneRight = function MovieHeaderPaneRight({
  children,
  ...restProps
}) {
  return <PaneRight {...restProps}>{children}</PaneRight>;
};
MovieHeader.SubTitle = function MovieHeaderSubTitle({
  children,
  ...restProps
}) {
  return <SubTitle {...restProps}>{children}</SubTitle>;
};
MovieHeader.Text = function MovieHeaderText({ children, ...restProps }) {
  return <Text {...restProps}>{children}</Text>;
};
MovieHeader.Frame = function MovieHeaderFrame({ children, ...restProps }) {
  return <Frame {...restProps}>{children}</Frame>;
};
MovieHeader.Div = function MovieHeaderDiv({ children, ...restProps }) {
  return <Div {...restProps}>{children}</Div>;
};
MovieHeader.Image = function MovieHeaderImage({ ...restProps }) {
  return <Image {...restProps} />;
};

export default MovieHeader;
