import React, { useState } from "react";
import { Wrapper, Title, Inner } from "./styles";
const PageName = ({ setType }) => {
  const [Movie, setMovie] = useState(false);
  const [tv, setTV] = useState(true);
  function handleClick(type, Id) {
    setType(type);
    setTV(false);
    setMovie(true);
  }
  function handleClicktv(type, Id) {
    setType(type);
    setMovie(false);
    setTV(true);
  }
  return (
    <Wrapper>
      <Inner>
        <Title
          id='MOVIE'
          state={Movie}
          onClick={() => handleClick("movie", "MOVIE")}>
          MOVIES
        </Title>
        <Title id='TV' state={tv} onClick={() => handleClicktv("tv", "TV")}>
          TV Series
        </Title>
      </Inner>
    </Wrapper>
  );
};

export default PageName;
