import React from "react";
import { IMAGE_URL, POSTER_SIZE } from "../../../api/config";
import Thumb from "../Thumb";
import { Wrapper, Container, Inner, Title, Name } from "./styles";
const Similar = ({ Similar }) => {
  return (
    <Wrapper>
      <Title>RELATED SHOWS</Title>
      <Container>
        {Similar.results.map((film) => {
          return (
            <Inner key={film.id}>
              <Thumb
                src={`${IMAGE_URL}${POSTER_SIZE}${film.poster_path}`}
                clickable
                movieId={film.id}
              />
              <Name>{film.name}</Name>
            </Inner>
          );
        })}
      </Container>
    </Wrapper>
  );
};

export default Similar;
