import React from "react";
import { Link } from "react-router-dom";
import { useMovieFetch } from "../../../hooks/useFetchMovies";
import { Image, Wrapper } from "./Thumb.styles";

const Thumb = ({ type, src, movieId, clickable }) => {
  return (
    <Wrapper>
      {clickable ? (
        <Link
          to={
            type === "movie"
              ? `/browse/movie/${movieId}`
              : `/browse/tv/${movieId}`
          }>
          <Image src={src} alt='movie_thumb' />
        </Link>
      ) : (
        <Image src={src} alt='movie_thumb' />
      )}
    </Wrapper>
  );
};

export default Thumb;
