import React from "react";
// Hooks
import { useParams } from "react-router-dom";
import { useMovieFetch } from "../hooks/useMovieFetch";

import MovieContainer from "../containers/movieHeader";
import NoImg from "../No-IMG.png";
import { Text } from "../components/Movies/Pagename/styles";
import { IMAGE_URL, POSTER_SIZE } from "../api/config";
//import Components
import { Spinner, Actors, GridTemplate } from "../components";
const Movie = () => {
  const { movieId } = useParams();
  const { state, loading, error } = useMovieFetch(movieId);
  if (loading) return <Spinner />;
  if (error) return <div>Something went wrong.....</div>;
  return (
    <>
      <MovieContainer movie={state} />
      <Text>ACTORS</Text>
      <GridTemplate>
        {state.actors.map((actor) => (
          <Actors
            key={actor.credit_id}
            imageUrl={
              actor.profile_path
                ? `${IMAGE_URL}${POSTER_SIZE}${actor.profile_path}`
                : NoImg
            }
            name={actor.name}
            character={actor.character}
          />
        ))}
      </GridTemplate>
    </>
  );
};

export default Movie;
