/* eslint-disable import/no-anonymous-default-export */
import {
  SEARCH_BASE_URL,
  API_KEY,
  API_URL,
  DISCOVER_GENRE_URL,
  GENRE_BASE_URL,
  TOPRATED_BASE_URL,
} from "./config";

export default {
  fetchSearchResults: async (searchTerm, page) => {
    const endpoint = `${SEARCH_BASE_URL}${searchTerm}&page=${page}`;
    return await (await fetch(endpoint)).json();
  },

  fetchDiscovery: async (genreId) => {
    const endpoint = `${DISCOVER_GENRE_URL}&with_genres=${genreId}`;
    return await (await fetch(endpoint)).json();
  },

  fetchGenres: async (searchTerm, page) => {
    const endpoint = searchTerm
      ? `${SEARCH_BASE_URL}${searchTerm}&page=${page}`
      : `${GENRE_BASE_URL}`;
    return await (await fetch(endpoint)).json();
  },

  fetchTop: async (searchTerm, page) => {
    const endpoint = searchTerm
      ? `${SEARCH_BASE_URL}${searchTerm}&page=${page}`
      : `${TOPRATED_BASE_URL}&page=${page}`;
    return await (await fetch(endpoint)).json();
  },

  fetchMovies: async (searchTerm, page, type) => {
    const endpoint = searchTerm
      ? `${SEARCH_BASE_URL}${searchTerm}&page=${page}`
      : `${API_URL}${type}/popular?api_key=${API_KEY}&language=en-US;&page=${page}`;
    return await (await fetch(endpoint)).json();
  },
  fetchMovie: async (movieId) => {
    const endpoint = `${API_URL}movie/${movieId}?api_key=${API_KEY}`;
    return await (await fetch(endpoint)).json();
  },
  fetchSeries: async (tvId) => {
    const endpoint = `${API_URL}tv/${tvId}?api_key=${API_KEY}&language=en-US`;
    return await (await fetch(endpoint)).json();
  },
  fetchSeason: async (tvId, seasonId) => {
    console.log(tvId);
    const endpoint = `${API_URL}tv/${tvId}/season/${seasonId}?api_key=${API_KEY}&language=en-US`;
    return await (await fetch(endpoint)).json();
  },

  fetchCredits: async (movieId, type) => {
    const creditsEndpoint = `${API_URL}${type}/${movieId}/credits?api_key=${API_KEY}`;
    return await (await fetch(creditsEndpoint)).json();
  },
  fetchSimilar: async (tvId) => {
    const endpoint = `${API_URL}tv/${tvId}/similar?api_key=${API_KEY}&language=en-US&page=1`;
    return await (await fetch(endpoint)).json();
  },
  fetchVideo: async (tvId, seasonId) => {
    const endpoint = !seasonId
      ? `${API_URL}tv/${tvId}/videos?api_key=${API_KEY}&language=en-US`
      : `${API_URL}tv/${tvId}/season/${seasonId}/videos?api_key=${API_KEY}&language=en-US`;
    return await (await fetch(endpoint)).json();
  },
};
//similar TV shows
// https://api.themoviedb.org/3/tv/{tv_id}/similar?api_key=<<api_key>>
// videos
//
// https://api.themoviedb.org/3/tv/{tv_id}/?api_key=<<api_key>>&language=en-US
// https://api.themoviedb.org/3/tv/{tv_id}/season/{season_number}/videos?api_key=<<api_key>>&language=en-US
// https://api.themoviedb.org/3/tv/{tv_id}/season/{season_number}/episode/{episode_number}/videos?api_key=<<api_key>>&language=en-US
//seasons
// https://api.themoviedb.org/3/tv/{tv_id}/?api_key=<<api_key>>&language=en-US
// tv images
// https://api.themoviedb.org/3/tv/{tv_id}/season/{season_number}/images?api_key=<<api_key>>&language=en-US
// https://api.themoviedb.org/3/tv/{tv_id}/season/{season_number}/episode/{episode_number}/images?api_key=<<api_key>>
//episodes
// https://api.themoviedb.org/3/tv/{tv_id}/season/{season_number}/episode/{episode_number}?api_key=<<api_key>>&language=en-US
