import styled from "styled-components/macro";

export const Wrapper = styled.div`
  width: 100vw;
  z-index: 2;
  top: 3rem;
  position: absolute;
  padding: 0.5rem;
  background-color: #483c3c44;
`;
export const Content = styled.div`
  font-size: clamp(10px, 2vw, 3rem);
  color: white;
  a {
    color: inherit;
    text-decoration: none;
  }
  span {
    margin-inline-end: 1em;
  }
`;
